import "@/styles/globals.css";
import Navbar from "@/components/Navbar";
import Footer from "@/components/Footer";
import { Toaster } from "react-hot-toast";
import { useRouter } from "next/router";
import { AnimatePresence, motion } from "framer-motion";

export default function App({ Component, pageProps }) {
  const router = useRouter();
  const isAdminPage = router.pathname.startsWith("/admin");

  return (
    <>
      {/* ‚úÖ Navbar & Footer appear only on non-admin pages */}
      {!isAdminPage && <Navbar />}

      {/* ‚úÖ Toast notifications */}
      <Toaster
        position="top-center"
        toastOptions={{
          duration: 2500,
          style: { background: "#fff", color: "#333", borderRadius: "10px" },
          success: { iconTheme: { primary: "#f97316", secondary: "#fff" } },
        }}
      />

      {/* ‚úÖ Page transition animation */}
      <AnimatePresence mode="wait">
        <motion.main
          key={router.route}
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -10 }}
          transition={{ duration: 0.25, ease: "easeOut" }}
          className="min-h-screen bg-gradient-to-br from-orange-50 to-white"
        >
          {/* ‚ö†Ô∏è Remove internal Navbar from any page (e.g. ProviderDashboard) */}
          <Component {...pageProps} />
        </motion.main>
      </AnimatePresence>

      {!isAdminPage && <Footer />}
    </>
  );
}
